import { Component, OnInit, Input, AfterContentInit } from '@angular/core';

@Component({
  selector: 'app-project-item',
  templateUrl: './project-item.component.html',
  styleUrls: ['./project-item.component.css']
})
export class ProjectItemComponent implements OnInit, AfterContentInit {
  @Input()
  public projectTitle: string;
  @Input()
  public projectImageLink: string;
  @Input()
  public projectDescription: string;
  @Input()
  public skillsList: string;
  @Input()
  public projectLink: string;
  constructor() { }

  ngOnInit() {
    console.log("Initiated project item");
    this.printProps();
  }
  printProps() {
    console.log(this.projectTitle);
    console.log(this.projectImageLink);
    console.log(this.projectDescription);
    console.log(this.skillsList);
    console.log(this.projectLink);

  }
  ngAfterContentInit() {
    console.log("after content init");
    this.printProps();
  }
}



